<template>
  <div class="page-editor editor-wrapper" v-loading="loading">
    <!---->
    <div class="editor-side-bar border-R">
      <el-tabs tab-position="left" v-model="activeSideBar" style="height: 100%;">
        <el-tab-pane v-for="(item, index) in sidebarMenus" :key="index" :name="item.value">
          <el-tooltip slot="label" class="item" effect="dark" :content="item.label" placement="right">
            <i :class="item.elementUiIcon"></i>
          </el-tooltip>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!--&&-->
    <div class="editor-page-edit-wrapper">
      <componentLibs v-if="activeSideBar === 'componentLibs'"/>
      <pageManage v-if="activeSideBar === 'pageManage'"/>
      <templateLibs v-if="activeSideBar === 'templateLibs'"/>
      <imageLibs v-if="activeSideBar === 'imageLibs'"/>
    </div>
    <!---->
    <div class="editor-main">
      <div class="control-bar-wrapper">
        <controlBar :scale.sync="canvasConfig.scale"  @import-psd-data="importPsdData" @showPreview="showPreviewFn" @save="save" />
      </div>
      <editorPan :scale.sync="canvasConfig.scale"/>
    </div>
    <!---->
    <div class="el-attr-edit-wrapper scrollbar-wrapper">
      <el-tabs v-model="activeAttr" stretch>
        <el-tab-pane label="" name="">
          <attrEdit></attrEdit>
        </el-tab-pane>
        <el-tab-pane label="" name="">
          <eventEdit></eventEdit>
        </el-tab-pane>
        <el-tab-pane label="" name="">
          <animationEdit></animationEdit>
        </el-tab-pane>
        <el-tab-pane label="JS" name="">
          <scriptEdit></scriptEdit>
        </el-tab-pane>
        <el-tab-pane label="" name="">
          <pageAttrEdit></pageAttrEdit>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!---->
    <previewPage v-if="showPreview" :pageId="id" @closePreview="showPreview = false"></previewPage>
  </div>
</template>

<script>
	import componentLibs from './components/component-libs/Index'
	import pageManage from './components/page-manage'
	import templateLibs from './components/template-libs'
	import imageLibs from './components/image-libs'
	import editorPan from './components/editor-panel/Index'
	// 
	import attrEdit from './components/attr-configure/attr-edit'
	import animationEdit from './components/attr-configure/animation-edit'
	import eventEdit from './components/attr-configure/event-edit'
	import pageAttrEdit from './components/attr-configure/page-attr-edit'
	import scriptEdit from './components/attr-configure/script-edit'

	import controlBar from './components/control-bar'

  import previewPage from '@client/components/preview-page'

	import {mapState} from 'vuex'

	export default {
		components: {
			componentLibs,
			pageManage,
			templateLibs,
			imageLibs,
			editorPan,
			attrEdit,
			animationEdit,
			eventEdit,
			pageAttrEdit,
			scriptEdit,
			controlBar,
			previewPage
		},
		data() {
			return {
				id: '', // id
				loading: false,
				showPreview: false,
				activeAttr: '',
				activeSideBar: 'componentLibs',
				sidebarMenus: [
					{
						label: '',
						value: 'componentLibs',
						elementUiIcon: 'el-icon-s-operation'
					},
					{
						label: '',
						value: 'pageManage',
						elementUiIcon: 'el-icon-document'
					},
					{
						label: '',
						value: 'templateLibs',
						elementUiIcon: 'el-icon-files'
					},
					{
						label: '',
						value: 'imageLibs',
						elementUiIcon: 'el-icon-picture-outline'
					}
				],
				canvasConfig: {
					scale: 1
				}
			}
		},
		computed: {
			...mapState({
				projectData: state => state.editor.projectData,
				activePageUUID: state => state.editor.activePageUUID,
				activeElementUUID: state => state.editor.activeElementUUID
			})
		},
		created() {
			this.$store.dispatch('setPrjectData')
			this.id = this.$route.query.id;
			this.initPageData();
		},
		methods: {
			/**
       * 
       */
			initPageData(){
				this.loading = true;
				this.$axios.get('/page/get/' + this.id).then(res => {
					this.loading = false;
					this.$store.dispatch('setPrjectData', {
						...res.body
					})
				}).catch(() => {
					this.loading = false;
        })
      },
			/**
			 * 
			 */
			save() {
				console.log(JSON.stringify(this.projectData))
			},
			showPreviewFn(){
				// 
				this.$axios.post('/page/' + this.id, {page: JSON.stringify(this.projectData)}).then(res => {
					this.showPreview = true
				})
      },
			/**
			 * 
			 */
			screenshots() {
				domtoimage.toJpeg(this.$refs['canvas-panel'], {quality: 0.95})
					.then((dataUrl) => {
						console.log(dataUrl)
						this.$store.commit('updateCoverImage', dataUrl)
					});
			},
			/**
			 *
			 * @param dataList
			 */
			importPsdData(dataList) {
				dataList.forEach(item => {
					let {width, height, top, left, imageSrc, opacity, zIndex} = item;
					console.log(width, height, top, left, imageSrc, opacity)
					setTimeout(() => {
						this.$store.dispatch('addElement', {
							elName: 'qk-image',
							defaultStyle: {
								width: width / 2,
								height: height / 2,
								top: top / 2,
								left: left / 2,
								zIndex: zIndex,
								opacity
							},
							needProps: {
								imageSrc: imageSrc
							}
						})
					}, 10)
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
  .editor-wrapper {
    display: flex;
    height: 100%;
    position: relative;
    .editor-side-bar {
      width: 55px;
    }
    .editor-page-edit-wrapper {
      width: 210px;
      padding: 0 1px;
    }
    .editor-main {
      flex: 1;
      background: #f0f2f5;
      position: relative;
    }
    .el-attr-edit-wrapper {
      width: 380px;
      padding: 0;
    }
  }

  .control-bar-wrapper {
    position: absolute;
    top: -44px;
    left: 0;
    width: 100%;
    z-index: 10;
    text-align: center;
  }
</style>
<style lang="scss">
  .editor-side-bar {
    .el-tabs__item.is-active {
      background: rgba(37, 165, 137, 0.09);
    }
  }

  .el-attr-edit-wrapper {
    .el-tabs {
      height: 100%;
      padding-left: 16px;
      padding-right: 16px;
      padding-bottom: 10px;
    }
    .el-tabs__content {
      height: calc(100% - 55px);
      & > div {
        height: 100%;
      }
      .attr-edit-inner {
        padding-left: 16px;
        padding-right: 16px;
      }
    }
  }
</style>
